<template>
  <v-container>
    <v-layout class="mt-2 pb-5">
      <v-flex>
        <div class="text-xs-center headline">ACCOUNT</div>
      </v-flex>
    </v-layout>
      <h3 class='mt-2 text-xs-center'>Account Details: -</h3>
      <!-- <v-layout row wrap class='mt-2'>
        <v-flex xs3 class='text-xs-left'>Name:</v-flex>
        <v-flex xs9 class='text-xs-left'>{{ username }}</v-flex>
      </v-layout> -->
      <v-layout class='mt-2 text-xs-center'>
        <v-flex>Email : {{ email }}</v-flex>
      </v-layout>
      <!-- <v-layout row wrap class='mt-2'>
        <v-flex xs3 class='text-xs-left'>Cell No.:</v-flex>
        <v-flex xs9 class='text-xs-left'>{{ mobile }}</v-flex>
      </v-layout>
      <v-layout row wrap class='mt-2'>
        <v-flex xs3 class='text-xs-left'>Address:</v-flex>
        <v-flex xs9 class='text-xs-left'>{{ address }}</v-flex>
      </v-layout> -->
      <v-layout row wrap class='mt-2 text-xs-center'>
        <v-flex>Trader ID : {{ pin }}</v-flex>
      </v-layout>
      <v-layout row wrap class='mt-2 text-xs-center'>
        <v-flex >User Type : {{ user_type }}</v-flex>
      </v-layout><br>
    <v-layout class="mt-5 pt-5">
      <v-flex class="text-xs-center">
        <v-btn fab depressed color="indigo darken-1" class="white--text" to="/settings">BACK</v-btn>
        <v-btn fab depressed color="indigo darken-1" class="white--text" to="/settings/account">Change</v-btn>
      </v-flex>
    </v-layout>
  </v-container>
</template>


<script>

export default {
  data () {
    return {
      username: '',
      email: localStorage.getItem('uid'),
      mobile: '',
      address: '',
      user_type: localStorage.getItem('user_type'),
      pin: localStorage.getItem('pin')
    }
  }
}
</script>



